import React, {useCallback, useState} from 'react';
import {calcArrayT, operatorT, REGEX_UTILITY} from './services/types';
import handelMath from './services/math';
const Keybord = React.lazy(() => import('./services/Keybord'));

const App = (props: {setDark: () => void}) => {
  const [calcString, setCalcString] = useState('0');

  //* CALC LOOP *//
  const handelCalc = useCallback((_array: calcArrayT) => {
    const opHelper = (_operator: operatorT) => (array.indexOf(_operator) !== -1 ? array.indexOf(_operator) : max);
    let array: calcArrayT = _array;
    const max = array.length + 1;
    const multiply = opHelper('×');
    const division = opHelper('÷');
    const add = opHelper('+');
    const subtract = opHelper('-');
    multiply !== max || division !== max
      ? (array = handelMath(array, Math.min(multiply, division))) && handelCalc(array)
      : add !== max || subtract !== max
      ? (array = handelMath(array, Math.min(add, subtract))) && handelCalc(array)
      : setCalcString(array.toString());
  }, []);

  //* ON CLICKS EVENTS *//
  const clickEqual = useCallback(() => {
    const calcArray = calcString.replace(REGEX_UTILITY.equal, '').split(' ') as calcArrayT;
    handelCalc(calcArray);
  }, [calcString, handelCalc]);

  const clickInput = useCallback(
    (input: string) => {
      let newString = calcString === 'Infinity' ? '0' : calcString;
      switch (input) {
        case '-':
          newString += newString === '' ? '-' : newString.match(REGEX_UTILITY.min) ? ' -' : '';
          break;
        case '+':
        case '×':
        case '÷':
          newString.match(REGEX_UTILITY.opMin)
            ? (newString = newString.replace(REGEX_UTILITY.opMin, input))
            : newString.match(REGEX_UTILITY.op)
            ? (newString = newString.replace(REGEX_UTILITY.op, input))
            : newString !== '' && (newString += ` ${input}`);
          break;
        case '.':
          !newString.match(REGEX_UTILITY.dot) && (newString += newString.match(REGEX_UTILITY.number) ? '.' : newString === '' ? '0.' : ' 0.');
          break;
        case '0':
          !newString.endsWith(' 0') && newString !== '0' && (newString += newString.match(REGEX_UTILITY.zero) ? ' 0' : '0');
          break;
        default:
          newString === '0'
            ? (newString = input)
            : newString.endsWith(' 0')
            ? (newString = newString.slice(0, -1) + input)
            : (newString += newString.match(REGEX_UTILITY.defaultInput) ? ` ${input}` : input);
          break;
      }
      setCalcString(newString);
    },
    [calcString]
  );

  const clickBackspace = useCallback(() => {
    const amountBackSpace = calcString.slice(0, calcString.endsWith('0.') ? -2 : -1).trimEnd();
    setCalcString((prev) => (prev === 'Infinity' ? '0' : amountBackSpace));
  }, [calcString]);

  const clickClear = useCallback(() => setCalcString('0'), []);

  return (
    <div id='calc'>
      <h1>React Calculator</h1>
      <label>
        <input id='display' value={calcString === 'Infinity' ? 'Cannot divide by zero' : calcString} readOnly />
      </label>
      <Btn id='clear' lable='AC' onClick={() => clickClear()} />
      <Btn id='back-space' lable='‹' onClick={() => clickBackspace()} />
      <Btn id='multiply' lable='×' onClick={() => clickInput('×')} />
      <Btn id='seven' lable='7' onClick={() => clickInput('7')} />
      <Btn id='eight' lable='8' onClick={() => clickInput('8')} />
      <Btn id='nine' lable='9' onClick={() => clickInput('9')} />
      <Btn id='divide' lable='÷' onClick={() => clickInput('÷')} />
      <Btn id='four' lable='4' onClick={() => clickInput('4')} />
      <Btn id='five' lable='5' onClick={() => clickInput('5')} />
      <Btn id='six' lable='6' onClick={() => clickInput('6')} />
      <Btn id='add' lable='+' onClick={() => clickInput('+')} />
      <Btn id='one' lable='1' onClick={() => clickInput('1')} />
      <Btn id='two' lable='2' onClick={() => clickInput('2')} />
      <Btn id='three' lable='3' onClick={() => clickInput('3')} />
      <Btn id='subtract' lable='-' onClick={() => clickInput('-')} />
      <Btn id='decimal' lable='.' onClick={() => clickInput('.')} />
      <Btn id='zero' lable='0' onClick={() => clickInput('0')} />
      <Btn id='equals' lable='=' onClick={() => clickEqual()} />
      <Btn id='dark-btn' lable='◑' onClick={() => props.setDark()} />
      <a id='creadit' target='blink' href='https://github.com/AvivAbachi' children='Design and Code by AvivAbachi@Gmail.com' />
      <React.Suspense fallback={null}>
        <Keybord clickInput={clickInput} clickEqual={clickEqual} clickBackspace={clickBackspace} clickClear={clickClear} />
      </React.Suspense>
    </div>
  );
};

interface BtnProps {
  lable?: string;
  id?: string;
  onClick?: () => void;
}

const Btn = React.memo((props: BtnProps) => <button id={props.id} onClick={props.onClick} children={props.lable} />);

export default App;
